@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body {
    margin: 0;
    font-family: 'Lato', sans-serif;
    background-color: #121212;
    color: white;
}

.header {
    width: 100%;
    background-color: #333333;
    height: 50px;
    margin: 26px 0;
}

.header .container {
    position: relative;
    height: 100%;
}

.header .container img {
    position: absolute;
    top:0;
    bottom: 0;
    margin: auto;
}

.card {
    box-sizing: border-box;
    width: calc(20% - 24px);
    border: 2px solid #64C940;
    margin:12px;
    background-color: #333333;
    padding:12px;
    position: relative;
}

.card.selected {
    border: 6px solid #1E8FAA;
}

.card img {
    width:100%;
    display: block;
}

.card p.card-info__name {
    margin:0;
    font-size: 18px;
    font-weight: bold;
}

.card p.card-info__name span {
    display:block;
    width: 100%;
    font-size: 12px;
    font-weight: normal;
}

.card p.card-info__generics {
    margin:0;
    font-size: 12px;
    font-weight: bold;
}

.card p.card-info__generics span {
    font-weight: normal;
}

.card button.favourite {
    position: absolute;
    top: 12px;
    left: 12px;

    background-color:#1E8FAA;
    color: white;
    border: 1px solid white;
    outline: none;
    cursor: pointer;
    display: none;
}

.card:hover button.favourite {
    display: block;
}

.container.grid {
    display: flex;
    flex-wrap: wrap;
    padding: 12px;
    position: relative;
}

.grid .loading {
    position: absolute;
    top:0;
    left:0;
    margin:auto;
    width: 100%;
    height: 100%;
    background-color: rgba(18, 18, 18, 0.5);
    min-height: calc(100vh - 110px);
}

.loading p {
    position: -webkit-sticky;
    position: sticky;
    top: 50%;
    text-align: center;
}

.container.paginator {
    display:flex;
    justify-content: flex-end;
    justify-items: center;
    margin-bottom:24px;
}

.paginator p, .paginator button {
    font-size:14px;
    color: white;
}

.paginator p {
    border-left: 1px solid white;
    border-right: 1px solid white;
    margin: 0 4px;
    padding: 0 4px;
}

.paginator p span {
    color: #64C940;
}

.paginator button {
    width: auto;
    padding: 0;
    outline: none;
    border:0;
    background-color: transparent;
    cursor: pointer;
    height: 18px;
    box-sizing: content-box;
}

.paginator button:hover {
    border-bottom: 1px solid white;
}

.container.search-box {
    margin-top:38px;
    margin-bottom:12px;
}

.search-box input {
    border: 0;
    border-bottom: 1px solid white;
    color:white;
    background-color: transparent;
    outline: none;
}

.search-box button {
    outline: none;
    border: 1px solid white;
    background-color: transparent;
    color: white;
    cursor: pointer;
}

.container {
    width:100%;
    max-width: 1266px;
    margin:auto;
    box-sizing: border-box;
    padding:0 24px;
}

